/* eslint-disable */
import React, { useState, useEffect, useCallback, useRef } from 'react'
import TonePill from './TonePill'
import eventBus, { EVENTS } from '../../utils/eventBus'

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || ''

// Auth fetch helper
const authFetch = (url) => {
  const token = localStorage.getItem('token');
  return fetch(url, {
    headers: {
      'Content-Type': 'application/json',
      ...(token ? { 'Authorization': `Bearer ${token}` } : {}),
    },
  });
};

const fmtUA = (n) => (Number(n) || 0).toLocaleString('uk-UA', { maximumFractionDigits: 0 })

// Debounce utility
const debounce = (fn, delay) => {
  let timeoutId;
  const debouncedFn = (...args) => {
    clearTimeout(timeoutId);
    timeoutId = setTimeout(() => fn(...args), delay);
  };
  debouncedFn.cancel = () => clearTimeout(timeoutId);
  return debouncedFn;
};

/**
 * LeftRailFinance -     
 *      
 * 
 * :
 * -   endpoint /api/finance/deposit-hold?order_id={id}  /api/finance/deposits
 * - Debounce  EventBus (300ms)   "" 
 */
export default function LeftRailFinance({
  orderId,             // ID     
  rentAmount = 0,      //    ( )
  depositAmount = 0,   //    (fallback)
  discountPercent = 0, //  
  discountAmount = 0,  //  
  rentBeforeDiscount = 0, //    
  serviceFee = 0,      //   ( )
  serviceFeeName = "", //  
  onServiceFeeChange,  // Callback    
  isEditable = true,   //  
}) {
  const [editingServiceFee, setEditingServiceFee] = useState(false)
  const [serviceFeeInput, setServiceFeeInput] = useState(serviceFee || 0)
  const [serviceFeeNameInput, setServiceFeeNameInput] = useState(serviceFeeName || " ")
  const [loading, setLoading] = useState(true)
  const [payments, setPayments] = useState([])
  const [deposit, setDeposit] = useState(null)
  const [refreshKey, setRefreshKey] = useState(0)
  
  // Ref  debounced 
  const debouncedRefreshRef = useRef(null)

  //    - 
  const fetchData = useCallback(() => {
    if (!orderId) {
      setLoading(false)
      return
    }

    setLoading(true)
    
    //  P0.1:   endpoint   
    Promise.all([
      authFetch(`${BACKEND_URL}/api/finance/payments?order_id=${orderId}`).then(r => r.json()),
      authFetch(`${BACKEND_URL}/api/finance/deposit-hold?order_id=${orderId}`).then(r => r.json())
    ])
    .then(([paymentsData, depositData]) => {
      setPayments(paymentsData.payments || [])
      // depositData        null
      setDeposit(depositData || null)
      setLoading(false)
    })
    .catch(err => {
      console.error('Failed to load finance data:', err)
      setLoading(false)
    })
  }, [orderId])

  //  
  useEffect(() => {
    fetchData()
  }, [fetchData, refreshKey])

  //  P0.3: Debounced refresh  EventBus
  useEffect(() => {
    //  debounced 
    debouncedRefreshRef.current = debounce(() => {
      setRefreshKey(k => k + 1)
    }, 300)
    
    return () => {
      // Cleanup debounce  unmount
      if (debouncedRefreshRef.current?.cancel) {
        debouncedRefreshRef.current.cancel()
      }
    }
  }, [])

  //      DEBOUNCE
  useEffect(() => {
    const handleFinanceUpdate = (data) => {
      //         
      if (!data || !data.orderId || data.orderId === orderId) {
        //  P0.3:  debounced refresh
        if (debouncedRefreshRef.current) {
          debouncedRefreshRef.current()
        }
      }
    }

    const unsubFinance = eventBus.on(EVENTS.FINANCE_UPDATED, handleFinanceUpdate)
    const unsubPayment = eventBus.on(EVENTS.PAYMENT_CREATED, handleFinanceUpdate)
    const unsubDeposit = eventBus.on(EVENTS.DEPOSIT_CREATED, handleFinanceUpdate)
    const unsubRefund = eventBus.on(EVENTS.DEPOSIT_REFUNDED, handleFinanceUpdate)
    const unsubGlobal = eventBus.on(EVENTS.REFETCH_ALL, handleFinanceUpdate)

    return () => {
      unsubFinance()
      unsubPayment()
      unsubDeposit()
      unsubRefund()
      unsubGlobal()
    }
  }, [orderId])

  //   
  const rentPayments = payments.filter(p => p.payment_type === 'rent')
  const rentPaid = rentPayments.reduce((sum, p) => sum + (p.amount || 0), 0)
  
  //   -      fin_deposit_holds
  const hasDeposit = deposit !== null && deposit !== undefined
  const depositHeld = hasDeposit ? (deposit.held_amount || 0) : 0
  const depositCurrency = hasDeposit ? (deposit.currency || 'UAH') : 'UAH'
  const depositActual = hasDeposit ? (deposit.actual_amount || depositHeld) : 0
  
  // 
  const rentStatus = rentPaid >= rentAmount ? 'paid' : rentPaid > 0 ? 'partial' : 'pending'
  const depositStatus = hasDeposit && depositHeld > 0 ? 'received' : 'pending'
  
  //  
  const isFullyPaid = rentStatus === 'paid' && depositStatus === 'received'
  const rentDue = Math.max(0, rentAmount - rentPaid)
  
  //    
  const depositDisplay = depositCurrency === 'UAH' 
    ? ` ${fmtUA(depositActual)}` 
    : `${depositActual} ${depositCurrency}`

  if (loading) {
    return (
      <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm">
        <div className="text-sm text-slate-500"> ...</div>
      </div>
    )
  }

  return (
    <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm">
      <div className="flex items-center justify-between mb-3">
        <h3 className="font-semibold text-slate-800">  </h3>
        <TonePill tone={isFullyPaid ? 'ok' : 'warn'}>
          {isFullyPaid ? '  ' : rentDue > 0 ? `  ${fmtUA(rentDue)}` : ' '}
        </TonePill>
      </div>
      
      <div className="space-y-3 text-sm">
        {/*  */}
        <div className="rounded-lg border border-slate-100 bg-slate-50 p-3">
          <div className="flex items-center justify-between">
            <span className="text-slate-600"></span>
            <span className="font-semibold text-slate-800"> {fmtUA(rentAmount)}</span>
          </div>
          
          {/*     */}
          {discountPercent > 0 && (
            <div className="mt-1 flex items-center justify-between text-xs">
              <span className="text-green-600">
                  {discountPercent}%
              </span>
              <span className="text-green-600 font-medium">
                - {fmtUA(discountAmount)}
              </span>
            </div>
          )}
          
          <div className="mt-1 flex items-center justify-between">
            <span className="text-xs text-slate-500">
              {rentStatus === 'paid' && ' '}
              {rentStatus === 'partial' && `  ${fmtUA(rentPaid)}`}
              {rentStatus === 'pending' && '  '}
            </span>
            {rentStatus === 'paid' && <TonePill tone="ok" size="sm"></TonePill>}
            {rentStatus === 'partial' && <TonePill tone="warn" size="sm"></TonePill>}
            {rentStatus === 'pending' && <TonePill tone="danger" size="sm"> </TonePill>}
          </div>
        </div>
        
        {/*  */}
        <div className="rounded-lg border border-slate-100 bg-slate-50 p-3">
          <div className="flex items-center justify-between">
            <span className="text-slate-600"></span>
            <span className="font-semibold text-slate-800">
              {depositStatus === 'received' ? depositDisplay : ` ${fmtUA(depositAmount)}`}
            </span>
          </div>
          <div className="mt-1 flex items-center justify-between">
            <span className="text-xs text-slate-500">
              {depositStatus === 'received' && ' '}
              {depositStatus === 'pending' && depositAmount > 0 ? ' ' : ''}
            </span>
            {depositStatus === 'received' && <TonePill tone="ok" size="sm"></TonePill>}
            {depositStatus === 'pending' && depositAmount > 0 && <TonePill tone="warn" size="sm"></TonePill>}
          </div>
          {depositStatus === 'received' && depositCurrency !== 'UAH' && (
            <div className="mt-1 text-xs text-slate-400">  {fmtUA(depositHeld)}</div>
          )}
        </div>
        
        {/*   ( ) */}
        <div className="rounded-lg border border-amber-200 bg-amber-50 p-3">
          <div className="flex items-center justify-between mb-1">
            <span className="text-slate-600 text-sm font-medium">
              {serviceFee > 0 ? (serviceFeeName || " ") : " "}
            </span>
            {!editingServiceFee ? (
              <button
                onClick={() => { 
                  setEditingServiceFee(true); 
                  setServiceFeeInput(serviceFee || 0);
                  setServiceFeeNameInput(serviceFeeName || " ");
                }}
                className="font-semibold text-amber-700 hover:underline text-sm"
              >
                {serviceFee > 0 ? ` ${fmtUA(serviceFee)}` : '+ '}
              </button>
            ) : (
              <span className="text-amber-600 text-xs">...</span>
            )}
          </div>
          
          {editingServiceFee && (
            <div className="mt-2 space-y-2 bg-white p-2 rounded border border-amber-300">
              <div>
                <label className="text-xs text-slate-500 block mb-1"> </label>
                <input
                  type="text"
                  value={serviceFeeNameInput}
                  onChange={(e) => setServiceFeeNameInput(e.target.value)}
                  placeholder=" "
                  className="w-full px-2 py-1 text-sm border rounded"
                  autoFocus
                />
              </div>
              <div>
                <label className="text-xs text-slate-500 block mb-1"> ()</label>
                <input
                  type="number"
                  value={serviceFeeInput}
                  onChange={(e) => setServiceFeeInput(Number(e.target.value) || 0)}
                  className="w-full px-2 py-1 text-sm border rounded"
                  min="0"
                  step="100"
                />
              </div>
              <div className="flex gap-2 justify-end">
                <button
                  onClick={() => setEditingServiceFee(false)}
                  className="px-3 py-1 text-xs bg-slate-200 text-slate-700 rounded hover:bg-slate-300"
                >
                  
                </button>
                <button
                  onClick={() => {
                    if (onServiceFeeChange) onServiceFeeChange(serviceFeeInput, serviceFeeNameInput);
                    setEditingServiceFee(false);
                  }}
                  className="px-3 py-1 text-xs bg-green-500 text-white rounded hover:bg-green-600"
                >
                  
                </button>
              </div>
            </div>
          )}
          
          {!editingServiceFee && serviceFee === 0 && (
            <div className="text-xs text-amber-600">
                 2000 
            </div>
          )}
        </div>

        {/*   */}
        {payments.length > 0 && (
          <details className="mt-2">
            <summary className="cursor-pointer text-xs font-medium text-slate-600 hover:text-slate-800">
                 ({payments.length})
            </summary>
            <div className="mt-2 space-y-1 text-xs border-l-2 border-slate-200 pl-2">
              {payments.map((p, idx) => (
                <div key={idx} className="flex items-center justify-between py-1">
                  <div>
                    <span className="font-medium">{p.payment_type === 'rent' ? '' : ''}</span>
                    {p.accepted_by_name && <span className="text-slate-400 ml-1"> {p.accepted_by_name}</span>}
                  </div>
                  <div className="font-mono text-slate-700">
                    {p.currency && p.currency !== 'UAH' ? `${p.amount} ${p.currency}` : ` ${fmtUA(p.amount)}`}
                  </div>
                </div>
              ))}
            </div>
          </details>
        )}
      </div>
    </div>
  )
}
